.teamMain{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-left: 6%;
    margin-top: 6%;
}
.teamList{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
    column-gap: 1rem;
    row-gap: 2rem;
    width: 95%;
    margin-top: 4%;
    
}
.teamList .hoverImage{
    width: 31%;
    display: flex;
    align-items: center;
    justify-content: center;
    background: url('../../assets/our team/grid\ behind\ images.svg');
    padding: 2% 0;
    transition: 0.5s ease-in-out;
    position: relative;
    cursor: pointer;
    transition: 1s ease-in-out;
    
}
.teamList .hoverImage img{
    object-fit: contain;
    width: 90%;
    /* width: 92%;
    height: 18rem;
    padding: 13px; */
    transition: 0.5s ease-in-out;
    
}


.teamList .hoverImage .caption{
    top: 0;
    position: absolute;
    left: 3rem;
    opacity: 0;
}

.teamList .hoverImage .caption h4{
   
    font-size: 2rem;
    font-weight: bold;
}
.teamList .hoverImage .caption div{
    top: 1rem;
   background-color: #5c972e;
   height: 0.2rem;
   margin-top: -2rem;
   width: 8rem;

}
.teamList .hoverImage .caption p{
    font-weight: bold;
    font-size: 1.2rem;
    line-height: 28px;
    letter-spacing: 1px;
    padding-left: 0.5rem;

}
.teamList .hoverImage:hover .caption{
 opacity: 1;
}
.teamList .hoverImage:hover img{
   filter: brightness(40%);
   }
   







